@import url(https://fonts.googleapis.com/css?family=Montserrat:700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Cabin&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

body {
  width: 100%;
  height: 100vh;
}

#root {
  width: 100%;
  height: 100%;
}
.app {
  width: 100%;
  height: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #1db954;
  overflow: hidden;
}
.input {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 392px;
  margin: 32px;
  background-color: #ffffff;
  border-radius: 20px;
  padding: 16px;
  box-sizing: border-box;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.32);
  -webkit-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out;
}

.input.focused {
  width: 432px;
}

.searchBar {
  display: flex;
  flex-direction: row;
  align-items: center;
  position: relative;
}

.input input {
  font-family: 'Cabin', sans-serif;
  color: #8d8d8d;
  width: 360px;
  height: 56px;
  border: none;
  background-color: #0000001a;
  border-radius: 16px;
  padding: 16px;
  font-size: 20px;
  outline-width: 0;
  box-sizing: border-box;
}

.input.focused button {
  opacity: 1;
  -webkit-transform: scale(1);
          transform: scale(1);
}

.input button {
  position: absolute;
  right: 0;
  opacity: 0;
  display: flex;
  height: 24px;
  width: 24px;
  margin-left: 16px;
  border: none;
  background-color: transparent;
  outline-width: 0;
  padding: 0;
  justify-content: center;
  align-items: center;
  fill: #8d8d8d;
  -webkit-transition: opacity .1s ease-in-out .1s, -webkit-transform .3s cubic-bezier(0.8,-0.85, 0.26, 1.89);
  transition: opacity .1s ease-in-out .1s, -webkit-transform .3s cubic-bezier(0.8,-0.85, 0.26, 1.89);
  transition: opacity .1s ease-in-out .1s, transform .3s cubic-bezier(0.8,-0.85, 0.26, 1.89);
  transition: opacity .1s ease-in-out .1s, transform .3s cubic-bezier(0.8,-0.85, 0.26, 1.89), -webkit-transform .3s cubic-bezier(0.8,-0.85, 0.26, 1.89);
  -webkit-transform: scale(0.3);
          transform: scale(0.3);
}

.input button:hover {
  cursor: pointer;
}

.input button:active {
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.32);
  -webkit-transform: translateY(2px);
          transform: translateY(2px);
}

.input button svg {
  padding-left: 4px;
  fill: #1db954;
}

.input .focused {
  font-family: 'Cabin', sans-serif;
  color: #8d8d8d;
  width: 100%;
  border-radius: 16px;
  padding: 64px 16px;
  font-size: 16px;
  text-align: center;
  box-sizing: border-box;
}
.input .results {
  position: absolute;
  padding-top: 8px;
  top: 64px;
}

.input .result {
  display: flex;
  height: 56px;
  width: 100%;
  flex-direction: row;
  align-items: center;
  border-radius: 8px;
  box-sizing: border-box;
  padding: 8px 16px;
  margin: 8px 0;
  overflow: hidden;
  -webkit-animation: popup .4s ease-in-out;
          animation: popup .4s ease-in-out;
}

@-webkit-keyframes popup {
  0% {
    -webkit-transform: scale(0.3);
            transform: scale(0.3);
    opacity: 0;
  }

  50% {
    opacity: 0;
  }

  90% {
    -webkit-transform: scale(1.01);
            transform: scale(1.01);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }
}

@keyframes popup {
  0% {
    -webkit-transform: scale(0.3);
            transform: scale(0.3);
    opacity: 0;
  }

  50% {
    opacity: 0;
  }

  90% {
    -webkit-transform: scale(1.01);
            transform: scale(1.01);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }
}

.input .result:last-child {
  margin: 8px 0 0 0;
}

.input .result:hover {
  background-color: #0000001a;
  cursor: pointer;
}

.input .result .cover {
  height: 40px;
  width: 40px;
  border-radius: 4px;
}

.input .result .info {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  font-family: 'Cabin', sans-serif;
  color: #8d8d8d;
  border-radius: 16px;
  margin-left: 16px;
  font-size: 14px;
}

.input .result .info .title {
  font-weight: bold;
  color: #000000;
  width: 312px;
  margin-bottom: 4px;
  font-size: 16px;
  white-space: nowrap;
}
:root {
  --spin: 1;
  --color : '#000000'
}

.roulette {
  position: absolute;
  bottom: -50vh;
  height: 100vh;
  width: 100vh;
  background-color: '#000000';
  background-color: var(--color);
}

.roulette.load {
  -webkit-animation: 
    rotate1 1s 0.5s ease-in-out forwards,
    rotate2 1s 2s ease-in-out forwards, 
    rotate3 1s 3.5s ease-in-out forwards,
    rotate4 1s 5s ease-in-out forwards, 
    rotate5 1s 6.5s ease-in-out forwards,
    spin 1.2s 8.3s cubic-bezier(0.39,-0.01, 0.58, 0.97) forwards;
          animation: 
    rotate1 1s 0.5s ease-in-out forwards,
    rotate2 1s 2s ease-in-out forwards, 
    rotate3 1s 3.5s ease-in-out forwards,
    rotate4 1s 5s ease-in-out forwards, 
    rotate5 1s 6.5s ease-in-out forwards,
    spin 1.2s 8.3s cubic-bezier(0.39,-0.01, 0.58, 0.97) forwards;
}

.roulette img {
  height: 100%;
  width: 100%;
  -webkit-filter: drop-shadow(0 0 3px rgba(0, 0, 0, 0.7));
          filter: drop-shadow(0 0 3px rgba(0, 0, 0, 0.7));
}

.roulette .container {
  height: 100%;
  width: 100%;
  position: absolute;
  display: flex;
  justify-content: center;
}

.roulette .container .cover {
  height: 22.6vh;
  width: 22.6vh;
}

.roulette.idle .container .cover {
  margin-top: calc(9.6vh + 22.8vh);
}

.roulette.load .container .cover {
  margin-top: calc(9.6vh + 22.8vh);
}

.roulette.shot .container .cover {
  margin-top: 9.7vh;
}

.roulette .container .cover img {
  height: 100%;
  width: 100%;
  border-radius: 11.3vh;
}

.roulette.load .container:nth-child(1) .cover {
  -webkit-animation: load 0.5s cubic-bezier(0.28, 1.07, 0.4, 0.99) forwards;
          animation: load 0.5s cubic-bezier(0.28, 1.07, 0.4, 0.99) forwards;
}
.roulette.load .container:nth-child(2) .cover {
  -webkit-animation: load 0.5s 1.5s cubic-bezier(0.28, 1.07, 0.4, 0.99) forwards;
          animation: load 0.5s 1.5s cubic-bezier(0.28, 1.07, 0.4, 0.99) forwards;
}
.roulette.load .container:nth-child(3) .cover {
  -webkit-animation: load 0.5s 3s cubic-bezier(0.28, 1.07, 0.4, 0.99) forwards;
          animation: load 0.5s 3s cubic-bezier(0.28, 1.07, 0.4, 0.99) forwards;
}
.roulette.load .container:nth-child(4) .cover {
  -webkit-animation: load 0.5s 4.5s cubic-bezier(0.28, 1.07, 0.4, 0.99) forwards;
          animation: load 0.5s 4.5s cubic-bezier(0.28, 1.07, 0.4, 0.99) forwards;
}
.roulette.load .container:nth-child(5) .cover {
  -webkit-animation: load 0.5s 6s cubic-bezier(0.28, 1.07, 0.4, 0.99) forwards;
          animation: load 0.5s 6s cubic-bezier(0.28, 1.07, 0.4, 0.99) forwards;
}
.roulette.load .container:nth-child(6) .cover {
  -webkit-animation: load 0.5s 7.5s cubic-bezier(0.28, 1.07, 0.4, 0.99) forwards;
          animation: load 0.5s 7.5s cubic-bezier(0.28, 1.07, 0.4, 0.99) forwards;
}

.roulette .container .cover img.shot {
  -webkit-animation: shot 0.1s ease-in;
          animation: shot 0.1s ease-in;
}

@-webkit-keyframes shot {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes shot {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@-webkit-keyframes load {
  0% {
    margin-top: calc(9.6vh + 22.8vh);
  }

  100% {
    margin-top: 9.7vh;
  }
}

@keyframes load {
  0% {
    margin-top: calc(9.6vh + 22.8vh);
  }

  100% {
    margin-top: 9.7vh;
  }
}

@-webkit-keyframes rotate1 {
  0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(60deg);
            transform: rotate(60deg);
  }
}

@keyframes rotate1 {
  0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(60deg);
            transform: rotate(60deg);
  }
}
@-webkit-keyframes rotate2 {
  0% {
    -webkit-transform: rotate(60deg);
            transform: rotate(60deg);
  }
  100% {
    -webkit-transform: rotate(120deg);
            transform: rotate(120deg);
  }
}
@keyframes rotate2 {
  0% {
    -webkit-transform: rotate(60deg);
            transform: rotate(60deg);
  }
  100% {
    -webkit-transform: rotate(120deg);
            transform: rotate(120deg);
  }
}
@-webkit-keyframes rotate3 {
  0% {
    -webkit-transform: rotate(120deg);
            transform: rotate(120deg);
  }
  100% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }
}
@keyframes rotate3 {
  0% {
    -webkit-transform: rotate(120deg);
            transform: rotate(120deg);
  }
  100% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }
}
@-webkit-keyframes rotate4 {
  0% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }
  100% {
    -webkit-transform: rotate(240deg);
            transform: rotate(240deg);
  }
}
@keyframes rotate4 {
  0% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }
  100% {
    -webkit-transform: rotate(240deg);
            transform: rotate(240deg);
  }
}
@-webkit-keyframes rotate5 {
  0% {
    -webkit-transform: rotate(240deg);
            transform: rotate(240deg);
  }
  100% {
    -webkit-transform: rotate(300deg);
            transform: rotate(300deg);
  }
}
@keyframes rotate5 {
  0% {
    -webkit-transform: rotate(240deg);
            transform: rotate(240deg);
  }
  100% {
    -webkit-transform: rotate(300deg);
            transform: rotate(300deg);
  }
}
@-webkit-keyframes rotate6 {
  0% {
    -webkit-transform: rotate(300deg);
            transform: rotate(300deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes rotate6 {
  0% {
    -webkit-transform: rotate(300deg);
            transform: rotate(300deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(300deg);
            transform: rotate(300deg);
  }
  100% {
    -webkit-transform: rotate(calc(360deg * 5 + 60deg * 1));
            transform: rotate(calc(360deg * 5 + 60deg * 1));
    -webkit-transform: rotate(calc(360deg * 5 + 60deg * var(--spin)));
            transform: rotate(calc(360deg * 5 + 60deg * var(--spin)));
  }
}

@keyframes spin {
  0% {
    -webkit-transform: rotate(300deg);
            transform: rotate(300deg);
  }
  100% {
    -webkit-transform: rotate(calc(360deg * 5 + 60deg * 1));
            transform: rotate(calc(360deg * 5 + 60deg * 1));
    -webkit-transform: rotate(calc(360deg * 5 + 60deg * var(--spin)));
            transform: rotate(calc(360deg * 5 + 60deg * var(--spin)));
  }
}

